import React, { useState } from "react";
import Preloader from "../../common/Preloader/Preloader";
import s from "./ProfileInfo.module.css";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import userPhoto from "./../../../assets/images/icon-256x256.png";
import ProfileDataForm from "./ProfileDataForm";

const ProfileInfo = ({
  profile,
  status,
  updateStatus,
  isOwner,
  savePhoto,
  saveProfile,
}) => {
  let [editMode, setEditMode] =
    useState(false); /* editMode  false  */

  /* if(props.profile == null || props.profile) */
  if (!profile) {
    return <Preloader />;
  }

  const onMainPhotoSelected = (e) => {
    /*       */
    if (e.target.files.length) {
      /*             */
      savePhoto(e.target.files[0]);
    }
  };

  const onSubmit = (formData) => {
    /*        */
    saveProfile(formData).then(() => {
      setEditMode(false);
    });
    /*    false      */
  };

  return (
    <div>
      {/*       <div className={s.profileImage}>
        <img src="https://helpx.adobe.com/content/dam/help/en/photoshop/using/convert-color-image-black-white/jcr_content/main-pars/before_and_after/image-before/Landscape-Color.jpg" />
      </div> */}
      <div className={s.descriptionBlock}>
        <div>
          <img
            src={profile.photos.large || userPhoto}
            className={s.mainPhoto}
          />{" "}
          {/*     API    ( API   ) */}
          {isOwner && (
            <div className={s.choosePhoto}>
              <input
                type={"file"}
                onChange={
                  onMainPhotoSelected
                } /*      onChange */
              />
            </div>
          )}
        </div>
        <div className={s.edit}>
          {editMode ? (
            <ProfileDataForm
              initialValues={
                profile
              } /*   edit     */
              onSubmit={onSubmit}
              profile={profile}
            />
          ) : (
            <ProfileData
              profile={profile}
              isOwner={isOwner}
              goToEditMode={() => {
                setEditMode(true);
              }} /* setEditMode   true */
            />
          )}{" "}
          {/*     ProfileDataForm     ProfileData */}
          {/*       */}
          <ProfileStatusWithHooks status={status} updateStatus={updateStatus} />
        </div>
      </div>
    </div>
  );
};

const ProfileData = ({ profile, isOwner, goToEditMode }) => {
  return (
    <div>
      {isOwner && (
        <div>
          <button className={s.contactBtn} onClick={goToEditMode}>
            Edit profile
          </button>
        </div>
      )}{" "}
      {/*   isOwner        */}
      <div>
        <b>Full name</b>: {profile.fullName} {/* */}
      </div>
      <div>
        <b>Looking for a job</b>: {profile.lookingForAJob ? "yes" : "no"}{" "}
        {/*     profile.lookingForAJob true  yes  false  no */}
      </div>
      {profile.lookingForAJob && (
        <div>
          <b>My professional skills</b>: {profile.lookingForAJobDescription}{" "}
          {/*    lookingForAJobDescription */}
        </div>
      )}
      <div>
        <b>About me</b>: {profile.aboutMe}{" "}
        {/*     */}
      </div>
      <div>
        <b>Contacts</b>:{" "}
        {Object.keys(profile.contacts).map((key) => {
          return (
            <Contact
              key={key}
              contactTitle={key}
              contactValue={profile.contacts[key]}
            />
          );
        })}{" "}
        {/*   Object.keys       
    .map(key => {})        Contact*/}
      </div>
    </div>
  );
};

/*        */
const Contact = ({ contactTitle, contactValue }) => {
  return (
    <div className={s.contact}>
      <b>{contactTitle}</b>: {contactValue}
    </div>
  );
};

export default ProfileInfo;
