import axios from "axios";

const instance = axios.create({
  /*    axios */ withCredentials: true,
  baseURL: "https://social-network.samuraijs.com/api/1.0/",
  headers: {
    "API-KEY": "c4336df1-8ee5-44e0-8988-9078117aeb3c",
  },
});

/*    axios    instance             instance*/
export const usersAPI = {
  getUsers(currentPage = 1, pageSize = 10) {
    /*       */
    return instance
      .get(`users?page=${currentPage}&count=${pageSize}`)
      .then((response) => {
        return response.data;
      }); /*     response           data   */
  },
  follow(userId) {
    return instance.post(`follow/${userId}`);
  },
  unfollow(userId) {
    return instance.delete(
      `follow/${userId}`
      /*     id  default   delete 
     delete {}       */
    );
  },
  getProfile(userId) {
    console.warn(
      "Obsolete method. Please use profileAPI object"
    ); /*                 profileAPI*/
    return profileAPI.getProfile(userId); /*    profileAPI*/
  },
  /* this.          this
props               (currentPage, pageSize)        
*/
};

export const profileAPI = {
  getProfile(userId) {
    return instance.get(
      `profile/` + userId
    ); /*    profileContainer */
  },
  getStatus(userId) {
    return instance.get(
      `profile/status/` + userId
    ); /*  get    */
  },
  updateStatus(status) {
    return instance.put(`profile/status`, {
      status: status,
    }); /*  put     -       
    put    {status: status}      API
    */
  },
  savePhoto(photoFile) {
    const formData =
      new FormData(); /*           JSON     */
    formData.append(
      "image",
      photoFile
    ); /* append    image   API    photoFile   */
    return instance.put(`profile/photo`, formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    }); /*      formData,      headers: {
      "Content-Type": "multipart/form-data",
    },     */
  },
  saveProfile(profile) {
    /*    */
    return instance.put(`profile`, profile);
  },
};

/*  HeaderContainer */
export const authAPI = {
  me() {
    return instance.get(`auth/me`);
  },
  /*   API      
      */
  login(email, password, rememberMe = false, captcha = null) {
    return instance.post(`auth/login`, {
      email,
      password,
      rememberMe,
      captcha,
    });
  },
  logout() {
    return instance.delete(`auth/login`);
  },
}; /* login(email, password, rememberMe = false)       false */

/*  Captcha */
export const securityAPI = {
  getCaptchaUrl() {
    return instance.get(`security/get-captcha-url`);
  },
};
