import { stopSubmit } from "redux-form";
import { authAPI, securityAPI } from "../api/api";

/* 3) action type  */
const SET_USER_DATE =
  "learn-react/auth/SET_USER_DATE"; /*    */
const GET_CAPTCHA_URL_SUCCESS =
  "learn-react/auth/GET_CAPTCHA_URL_SUCCESS"; /*    */

/* ==================================================================== */

/*1)    API */

let initialState = {
  userId: null /*  API   null  */,
  email: null,
  login: null,
  isAuth: false /* 12)   ? */,
  captchaUrl: null /*    */,
};

/* 2)  reducer */
const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATE:
    case GET_CAPTCHA_URL_SUCCESS /*        */:
      return {
        ...state,
        ...action.payload /*     userId  email login*/,
      };
    default:
      return state;
  }
};

/* ==================================================================== */

/* 4)  AC */
/* ACTION CREATOR */

export const setAuthUserData = (userId, email, login, isAuth) => ({
  type: SET_USER_DATE,
  payload: { userId, email, login, isAuth },
});

export const getCaptchaUrlSuccess = (captchaUrl) => ({
  type: GET_CAPTCHA_URL_SUCCESS,
  payload: { captchaUrl },
});

/* ==================================================================== */

/* THUNK */

/*  */
export const getAuthUserData = () => async (dispatch) => {
  let response = await authAPI.me();
  if (response.data.resultCode === 0) {
    let { id, email, login } = response.data.data;
    /*  (response.data.resultCode === 0)           setAuthUserData*/
    dispatch(
      setAuthUserData(id, email, login, true)
    ); /* true   */
  }
};

/*   login*/
export const login =
  (email, password, rememberMe, captcha) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe, captcha);
    if (response.data.resultCode === 0) {
      dispatch(getAuthUserData()); /*     */
    } else {
      if (response.data.resultCode === 10) {
        /* response.data.resultCode === 10      */
        dispatch(
          getCaptchaUrl()
        ); /*  url  url   captchaUrl: null  */
      }
      let message =
        response.data.messages.length > 0
          ? response.data.messages[0]
          : "Some error"; /*             Some error */
      dispatch(
        stopSubmit("login", { _error: message })
      ); /*   message      
      ("login", { _error: message }) login     { _error: message }   
      */
    }
  };

/*   login*/
export const logout = () => async (dispatch) => {
  let response = await authAPI.logout();
  if (response.data.resultCode === 0) {
    dispatch(
      setAuthUserData(null, null, null, false)
    ); /*    false   */
  }
};

/*   Captcha*/
export const getCaptchaUrl = () => async (dispatch) => {
  const response = await securityAPI.getCaptchaUrl();
  const captchaUrl = response.data.url; /*    url  */
  dispatch(
    getCaptchaUrlSuccess(captchaUrl)
  ); /*  AC     */
};

export default authReducer;

/* data =  (userId, email, login) */

/* 5)   reducer    redux-store */
