import { usersAPI } from "../api/api";
import { updateObjectInArray } from "../utils/validators/objects-helpers";

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const SET_CURRENT_PAGE = "SET_CURRENT_PAGE";
const SET_TOTAL_USERS_COUNT = "SET_TOTAL_USERS_COUNT";
const TOGGLE_IS_FETCHING = "TOGGLE_IS_FETCHING";
const TOGGLE_IS_FOLLOWING_PROGRESS =
  "TOGGLE_IS_FOLLOWING_PROGRESS"; /*   following */

/* ==================================================================== */

let initialState = {
  users: [],
  pageSize: 10,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: true /* ?   */,
  followingInProgress:
    [] /*                     */,
};

const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW:
      /* let stateCopy = { ...state, users: [...state.users] };    */
      /* let stateCopy = { ...state, users: state.users.map((u) => u) }; */
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {
          followed: true,
        }),
      };
    case UNFOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {
          followed: false,
        }),
      };
    case SET_USERS: {
      return {
        ...state,
        users: action.users,
      };
    }
    case SET_CURRENT_PAGE: {
      return {
        ...state,
        currentPage: action.currentPage,
      };
    }
    case SET_TOTAL_USERS_COUNT: {
      return {
        ...state,
        totalUsersCount: action.count,
      };
    }
    case TOGGLE_IS_FETCHING: {
      /*   true false */
      return {
        ...state,
        isFetching: action.isFetching,
      };
    }
    case TOGGLE_IS_FOLLOWING_PROGRESS: {
      /*   true false */
      return {
        ...state,
        followingInProgress: action.isFetching
          ? [...state.followingInProgress, action.userId]
          : [
              state.followingInProgress.filter((id) => id != action.userId),
            ] /*    id    action       */,
      };
    }
    default:
      return state;
  }
};

/* ==================================================================== */
/* ACTION CREATOR */

export const followSuccess = (userId) => ({ type: FOLLOW, userId });

export const unfollowSuccess = (userId) => ({
  type: UNFOLLOW,
  userId,
});

export const setUsers = (users) => ({
  type: SET_USERS,
  users,
});

export const setCurrentPage = (currentPage) => ({
  type: SET_CURRENT_PAGE,
  currentPage,
});

export const setTotalUsersCount = (totalUsersCount) => ({
  type: SET_TOTAL_USERS_COUNT,
  count: totalUsersCount,
});

export const toggeleIsFetching = (isFetching) => ({
  /*   true false */ type: TOGGLE_IS_FETCHING,
  isFetching,
});

export const toggeleFollowingProgress = (isFetching, userId) => ({
  /*   true false */ type: TOGGLE_IS_FOLLOWING_PROGRESS,
  isFetching,
  userId,
});

/*  thunk    */
export const getUsers = (currentPage, pageSize) => {
  return async (dispatch) => {
    /*   BLL getUsers        */
    dispatch(toggeleIsFetching(true));
    dispatch(setCurrentPage(currentPage));
    /*      DAL       API     response   data   */
    let data = await usersAPI.getUsers(currentPage, pageSize);
    dispatch(toggeleIsFetching(false));
    dispatch(setUsers(data.items)); /*    */
    dispatch(setTotalUsersCount(data.totalCount)); /*    */
  };
};

const followUnfollowFlow = async (
  dispatch,
  userId,
  apiMethod,
  actionCreator
) => {
  dispatch(toggeleFollowingProgress(true, userId));
  /*           */

  let response = await apiMethod(userId);
  if (response.data.resultCode == 0) {
    dispatch(
      actionCreator(userId)
    ); /*           */
  }
  dispatch(toggeleFollowingProgress(false, userId));
};

/*  thunk    */
export const follow = (userId, pageSize) => {
  return async (dispatch) => {
    followUnfollowFlow(
      dispatch,
      userId,
      usersAPI.follow.bind(usersAPI),
      followSuccess
    );
  };
};

/*  thunk    */
export const unfollow = (userId, pageSize) => {
  return async (dispatch) => {
    followUnfollowFlow(
      dispatch,
      userId,
      usersAPI.unfollow.bind(usersAPI),
      unfollowSuccess
    );
  };
};

/* getUsersThunkCreator   -  (currentPage, pageSize)    thunk  return (dispatch)*/

export default usersReducer;
